import React from 'react'
import './about.css'

const About = () => {
  return (
    <div className="container">
        <div className="video-column">
      {/* Video content */}
      <iframe
        title="Sample Video"
        width="600"
        height="480"
        src="https://www.youtube.com/embed/your-video-id"
        frameBorder="0"
        allowFullScreen
      ></iframe>
    </div>
    <div className="text-column">
      {/* Text content */}
      <h2>Why should you Partner with Afrima for the next Edition by 2025 as stated by <span> Mike Dada </span>- President, Afrima</h2>
     
    </div>
    
  </div>
    
  );
}


export default About
