import React from 'react' 
import { Link } from "react-router-dom"


export const HomeCard = ({item:{id, cover, name, rating, time, desc,starring, event,venue, videos}  }) => {
  return (
    <>
      <div className='box'>
        <div className='coverImage'>
            <img src={cover} alt='' />
        </div>
        <div className='content flex'>
          <div className='details row'>
          <h1>{name}</h1>
          <div className='rating flex'>
            <i className='fa fa-star' ></i>
            <i className='fa fa-star' ></i>
            <i className='fa fa-star' ></i>
            <i className='fa fa-star' ></i>
            <i className='fa fa-star' ></i>
          </div>
          <label>{rating}</label>
          <span>GP</span>
          <label>{time}</label>
        
        <p>{desc}</p>
        <div className='cast'>
          <h4>
            <span>Starring:</span>
            {starring}
          </h4>
          <h4>
            <span>Event:</span>
            {event}
          </h4>
          <h4>
            <span>Venue:</span>
            {venue}
          </h4>
        </div>
        <button className='primary-btn'>
          <i className='fas fa-play'></i>PLAY NOW
        </button>
        </div>
       
        <div className='playButton row'>
          <Link to={`/singlepage/${id}`}>
            <button>
              <div className='img'>
                <img src='assets/play.png' alt='' />
                <img src='assets/play-button.png' alt='' className='change' />
              </div>
              WATCH EVENT
            </button>
          </Link>
        </div>
      </div>
      </div>
    </>
  )
}

export default HomeCard
